<template>
    <div class="card shadow my-5 p-3">
        <h1 class="card-title mx-auto">dmd-life </h1>
    </div>
    <div class="card shadow my-5 p-3">
        <form action="" @submit.prevent="onSubmit" class="input-group justify-content-middle flex-column align-items-center">
            <div class="mb-3 col-12 col-md-6">
                <label for="formGroupExampleInput" class="form-label"></label>
                <input type="email" v-model="email" class="form-control" id="formGroupExampleInput" placeholder="login">
                <small v-if="eError">{{ eError }}</small>
            </div>
            <div class="mb-3 col-12 col-md-6">
                <label for="formGroupExampleInput2" class="form-label"></label>
                <input type="password" v-model="password" class="form-control" id="formGroupExampleInput2" placeholder="password">
                <small v-if="pError">{{ pError }}</small>
            </div>
            <div class="mb-3">
                <button class="btn btn-primary" type="submit"></button>
            </div>
        </form>
    </div>
</template>

<script>
    import * as yup from 'yup'
    import { useField, useForm } from 'vee-validate'
    import { useStore } from 'vuex'
    import { useRouter } from 'vue-router'
    import router from '../router'

    export default {
        setup() {
            const { handleSubmit, isSubmitting } = useForm()
            const store = useStore()

            const { value: email, errorMessage: eError, handleBlur: eBlur } = useField(
                'email',
                yup
                    .string()
                    .trim()
                    .required(' email')
                    .email('  email')
            )

            const { value: password, errorMessage: pError, handleBlur: pBlur } = useField(
                'password',
                yup
                    .string()
                    .trim()
                    .required(' ')
                    .min(6, '   6 ')
            )

            const onSubmit = handleSubmit(async values => {
                await store.dispatch('auth/login', values)
                router.push('/admin-home')
            })
            return {
                email,
                password,
                eError,
                pError,
                eBlur,
                pBlur,
                onSubmit,
                isSubmitting,
            }
        }
    }
</script>